<template>
  <div>
    <h1 v-if="!personInfo&&succeed===false">正在校验身份...</h1>
    <img width="100%" v-if="personInfo.length === 0&& succeed===true" src="https://img95.699pic.com/photo/40071/3839.jpg_wh300.jpg" alt="无权限">
  </div>

</template>
<script>
export default {
  data () {
    return {
      code: '',
      personInfo: '',
      succeed: false
    }
  },
  created () {
    this.getPersonInfo()
  },
  methods: {
    // 获取地址里面的参数
    getUrlParam (name) {
      const reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)') // 构造一个含有目标参数（code）的正则表达式对象
      const arr = window.location.search.substr(1).match(reg) // 匹配目标参数
      if (arr) {
        return decodeURIComponent(arr[2])// 取出code的值
      }
      return null
    },
    async getPersonInfo () {
      const code = this.getUrlParam('code')
      const { data: res } = await this.$http.get('/getUserInfo', {
        params: {
          code
        }
      })
      // 返回值是数组
      this.personInfo = res
      if (res.length !== 0) {
        this.$store.commit('initClassList', res)
        await this.$router.push('/home')
      }
      this.succeed = true
    }
  }
}
</script>

<style scoped>
h1{
  text-align: center;
}
</style>
